.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

td,
th {
  padding: 0.5em;
  border: 1px solid #555;
}

th {
  background: #aaa;
  color: white;
}

table {
  border-collapse: collapse;
  margin: 1em;
}

table.performance th {
  text-align: left;
}

table.performance td {
  text-align: right;
  font-family: "Consolas", "Courier New";
}

table.matched-trades td {
  font-family: "Consolas", "Courier New";
  text-align: right;
}

table.matched-trades tr:nth-child(odd) {
  background: #efefef;
}

.small-font {
  font-size: 0.8em;
}

input[type="file"] {
  display: none;
}

label.file {
  padding: 0.5em;
  cursor: pointer;
  display: inline-block;
  background: royalblue;
  color: white;
  font-weight: bold;
  font-size: 1em;
}

button.dates {
  border: none;
  padding: 0.5em;
  cursor: pointer;
  display: inline-block;
  background: mediumseagreen;
  color: white;
  font-weight: bold;
  font-size: 16px;
  margin: 1em;
  height: 37px;
}

.link {
  cursor: pointer;
  color: dodgerblue;
}

.link:hover {
  color: blue;
}

.react-calendar__tile::after {
  content: "";
  display: block;
  width: 0.75em;
  height: 0.75em;
  border-radius: 50%;
  margin-left: calc(50% - 0.25em - 1px);
  border: 1px solid transparent;
  background: transparent;
}

.react-calendar__tile.green::after {
  border: 1px solid white;
  background: green;
}

.react-calendar__tile.red::after {
  border: 1px solid white;
  background: red;
}

.sortable {
  cursor: pointer;
}

.sortable .up,
.sortable .down,
.sortable .none {
  display: inline-block;
  margin-left: 0.5em;
}

.sortable .up {
  transition: all 0.25s;
}

.sortable .down {
  transform: rotate(180deg);
  transition: all 0.25s;
}

.sortable .none {
  color: transparent;
}
